.container__game {
    height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container__game > h1 {
    text-align: center;
}

.container__game > img {
    width: 300px;
    filter: grayscale(100%) brightness(0%);
    -webkit-filter: grayscale(100%) brightness(0);
}

.container__game > div {
    margin-top: 1rem;
}

@media screen and (max-width:768px) {
    .main__game {
        height: auto;
    }

    .container__game {
        height: 650px;
    }
}

@media screen and (max-width:420px) {
    .container__game > h1 {
        font-size: 1.5rem;
    }

    .container__game > img {
        width: 250px;
    }
}